<template>
  <div>
    <mu-appbar :z-depth="0" textColor="black" color="white">
      一起听歌
      <connect-status slot="right" />
    </mu-appbar>
    <mu-container class="container">
      <mu-card class="card">
        <mu-card-title title="加入房间" sub-title="Join room"></mu-card-title>
        <mu-card-text>
          <mu-form ref="form" :model="form" @submit.prevent.stop="joinRoom">
            <mu-form-item label="房间 ID" prop="roomId">
              <mu-text-field type="number" min="1" max="9999999999" v-model="form.roomId" prop="roomId"></mu-text-field>
            </mu-form-item>
            <mu-form-item>
              <mu-button type="submit">加入</mu-button>
            </mu-form-item>
          </mu-form>
        </mu-card-text>
        <mu-card-actions>
          <mu-button flat @click="createRoom">创建房间</mu-button>
        </mu-card-actions>
      </mu-card>
      <mu-card class="card">
        <mu-card-title title="调试信息" sub-title="Debug info"></mu-card-title>
        <mu-card-text>
          <ul>
            <li v-for="(item, index) in logs" :key="index">{{ item }}</li>
          </ul>
        </mu-card-text>
      </mu-card>
    </mu-container>
  </div>
</template>

<script>
  import ConnectStatus from "@/components/ConnectStatus"

  export default {
    name: "HelloWorld",
    components: {
      ConnectStatus
    },
    data() {
      return {
        form: {
          id: ""
        },
        logs: []
      }
    },
    sockets: {
      joinedRoom(data) {
        this.$router.push("/room/" + data.room.id)
      },
      joinRoomErr(err) {
        this.log(err)
      }
    },
    mounted() {
      window.addEventListener('beforeunload', () => {
        this.$socket.close()
      })
    },
    methods: {
      createRoom() {
        this.$socket.emit("createRoom")
      },
      joinRoom() {
        this.$socket.emit("joinRoom", this.form.roomId)
      },
      log(msg) {
        this.logs.push(msg)
      }
    }
  };
</script>

